/* CategoryPage.css */
.category-page {
  padding: 80px;
  text-align: center;
}
.category-page h1{
  font-family: 'Playfair Display', serif;  /* Change to your preferred font */
  background: linear-gradient(135deg, #eb8a83, #f7c791);
  font-size: 35px;

}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 4px; /* ‚úÖ Adds space between cards */
}

.product-card-link {
  text-decoration: none;
  color: inherit;
}

.product-card {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  cursor: pointer;
  transition: box-shadow 0.3s ease;
  width: 250px; /* ‚úÖ Set a fixed width */
  margin: 10px; /* ‚úÖ Adds spacing between cards */
}

.product-card:hover {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.buy-button {
  display: inline-block;
  padding: 8px 12px;
  align-items: center; /* ‚úÖ Align icon & text */
  gap: 5px; /* ‚úÖ Add spacing between icon & text */
  align-items: center; /* ‚úÖ Align icon & text */
  color: rgb(243, 104, 39) !important; /* ‚úÖ Force text color */
  text-decoration: none !important; /* ‚úÖ Remove underline */
 /* ‚úÖ Keep border */
  border-radius: 5px !important;
  font-weight: bold !important;
  transition: all 0.3s ease !important;
  background: transparent !important; /* ‚úÖ Remove blue background forcefully */
}

.buy-button:hover {
   /* ‚úÖ Add black background on hover */
  color: rgb(17, 49, 231) !important; /* ‚úÖ Change text to white on hover */
}
/* üéØ Mobile-Specific Adjustments */
@media (max-width: 768px) {
  .category-page {
    padding: 30px 10px; /* Reduce padding for better spacing */
  }

  .product-list {
    gap: 6px; /* Reduce gap for compact layout */
    justify-content: center; /* Ensures perfect alignment */
  }

  .product-card {
    width: 95%; /* Allow cards to take full width */
    max-width: 220px; /* Keep them consistent */
    padding: 10px; /* Reduce padding for compact look */
    margin: 6px auto; /* Center cards properly */
  }

  .product-card h3 {
    font-size: 1rem; /* Adjust title size */
  }

  .product-card p {
    font-size: 0.85rem; /* Adjust text size */
  }

  /* üéØ Buy Button Adjustments */
  .buy-button {
    padding: 6px 10px; /* Reduce padding for mobile */
    font-size: 0.9rem; /* Adjust text size */
  }
}
