.popular-page {
    text-align: center;
    padding: 80px;
  }
  .popular-page h1 {
    font-family: 'Playfair Display', serif;  /* Change to your preferred font */
    background: linear-gradient(135deg, #c4f74f, #c9e97e);
    font-size: 35px;
  
  }
  .product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4px; /* ‚úÖ Adds space between cards */
  }

  
  .product-card-link {
    text-decoration: none;
    color: inherit;
  }
  
  .product-card {
    border: 1px solid #ddd;
    padding: 15px;
    border-radius: 8px;
    cursor: pointer;
    transition: box-shadow 0.3s ease;
    width: 250px; /* ‚úÖ Set a fixed width */
    margin: 10px; /* ‚úÖ Adds spacing between cards */
  }
  
  .product-card:hover {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  }
  /* üéØ Mobile-Specific Adjustments */
@media (max-width: 768px) {
  .popular-page {
    padding: 40px 20px; /* Reduce padding for better spacing */
  }

  .product-list {
    gap: 8px; /* Slightly adjust spacing */
  }

  .product-card {
    width: 95%; /* Ensure cards fit properly on mobile */
    padding: 12px; /* Reduce padding */
    margin: 8px 0; /* Adjust margin for better spacing */
  }

  .product-card h3 {
    font-size: 16px; /* Adjust text size for readability */
  }
}
